// Header style for main page
.main-header {
	margin: 0;
  padding: 0;
  padding-bottom: 50px;

	min-width: 320px;
	width: 100%;

  background-color: #f1f2f4;
}

.main-header__wrapper {
  margin: 0 auto;
  padding: 0;

  width: 320px;
}

.main-header__title {
	margin: 0;
	padding: 0;
	padding-top: 17px;
	padding-bottom: 16px;

	font-size: 24px;
	font-weight: 800;
	line-height: 24px;
  text-align: center;
  
  background-color: #ffffff;
}

.main-header__nav {
  display: flex;
  justify-content: space-between;
	margin: 0;
	padding: 0;

	background-color: #f1f2f4;
}

.main-header__nav-list {
	display: flex;

	margin: 0;
	margin-left: 15px;
	padding: 0;
	padding-top: 26px;

	list-style: none;
}

.main-header__nav-item {
	margin: 0;
	margin-right: 25px;
	padding: 0;

	&:last-child {
		margin-right: 0;
	}
}

.main-header__nav-link {
	margin: 0;
	padding: 0;

	font-size: 16px;
	font-weight: 600;
}

.main-header__user-nav {
  margin: 0;
  margin-top: 23px;
  margin-right: 15px;
  padding: 0;

  list-style: none;
}

.main-header__user-nav-item {
  display: inline-block;
  margin: 0;
  padding: 0;

  &:last-child {
    margin-left: 18px;
  }
}

.main-header__main-picture {
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 0;
  padding-top: 55px;
  padding-bottom: 44px;

  min-width: 242px;
  width: 242px;

  text-align: center;

  &::before {
    position: absolute;
    content: "";
    display: block;
    margin: 0;
    padding: 0;
    top: 35px;
    left: 0;

    width: 109px;
    height: 80px;

    background-image: linear-gradient(45deg, #f1f2f4, #f1f2f4 50%, #000000 50%,#000000 52%,#f1f2f4 52%);
    background-size: 8px 8px;
  }

  &::after {
    position: absolute;
    content: "";
    display: block;
    margin: 0;
    padding: 0;
    bottom: 27px;
    right: 0;

    width: 109px;
    height: 80px;

    background-image: linear-gradient(-45deg, #f1f2f4, #f1f2f4 50%, #000000 50%,#000000 52%,#f1f2f4 52%);
                              
    background-size: 8px 8px;
    }

  & img {
    position: relative;
    z-index: 2;
  }
}

.main-header__txt {
  margin: 0;
  margin-bottom: 2px;
  padding: 0;

  font-size: 18px;
  font-weight: normal;
  text-align: center;
  color: #727a82;
}

.main-header__footer {
  display: none;
}

@media(min-width: 768px) {
  .main-header {
    padding-bottom: 41px;
  }

  .main-header__title {
    padding-top: 22px;
    padding-bottom: 20px;
  }

  .main-header__wrapper {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;

    padding-bottom: 9px;

    width: 678px;

    &--column {
      order: 1;
      
      flex-direction: column;

      margin: 0;
      padding: 0;
      padding-top: 98px;
  
      width: 56.7%;
    }
  }

  .main-header__nav {
    width: 100%;
  }

  .main-header__nav-list {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-top: 42px;
    padding: 0;

    width: 197px;
  }

  .main-header__nav-item {
    margin-right: 0;
  }

  .main-header__user-nav {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-top: 38px;
    padding: 0;

    width: 73px;
  }

  .main-header__main-picture {
    order: 2;

    margin: 0;
    padding-top: 24px;

    width: 264px;
    height: 319px;

    &::before {
      top: -6px;
      left: -31px;

      width: 141px;
      height: 104px;

      background-image: repeating-linear-gradient(45deg,#f1f2f4,#f1f2f4 7px,#000000 7px,#000000 8px);
      background-size: auto;
  
    }

    &::after {
      right: -30px;
      bottom: 14px;
      width: 141px;
      height: 104px;
      background-image: repeating-linear-gradient(-45deg,#f1f2f4,#f1f2f4 7px,#000000 7px,#000000 8px);
      background-size: auto;
    }
  }

  .main-header__txt {
    margin-left: 88px;
    padding-bottom: 3px;

    font-size: 18px;
    text-align: left;
  }

  .main-header__footer {
    display: flex;
    justify-content: space-between;

    margin: 0 auto;
    padding: 0;

    width: 678px;
  }

  .main-header__footer-menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    margin: 0;
    margin-top: 13px;
    padding: 0;

    width: 170px;
  
    list-style: none;
  }

  .main-header__footer-link {
    margin: 0;
    padding: 0;

    font-size: 14px;
  }
}

@media(min-width: 1366px) {
  .main-header {
    padding-bottom: 48px;
  }

  .main-header__title {
    padding-bottom: 21px;
  }

  .main-header__wrapper {
    width: 1170px;

    &--column {
      padding-top: 8px;
      width: 570px;
    }
  }

  .main-header__nav-list {
    flex-direction: column;

    margin-top: 46px;
  }
  
  .main-header__nav-item {
    margin-bottom: 27px;
  }

  .main-header__user-nav {
    flex-direction: column-reverse;
    align-items: flex-end;
    
    margin-top: 50px;

    width: 21px;
    height: min-content;
  }

  .main-header__user-nav-item {
    margin: 0;
    padding: 0;

    &:last-child {
      margin-bottom: 31px;
    }
  }

  .main-header__main-picture {
    margin: 0;
    margin-top: -131px;
    padding: 0;
    width: 430px;
    height: 517px;

    &::before {
      top: -50px;
      left: -50px;

      width: 232px;
      height: 176px;

      background-image: repeating-linear-gradient(45deg,#f1f2f4,#f1f2f4 14px,#000 14px,#000 15px);
    }

    &::after {
      right: -50px;
      bottom: -50px;
      width: 232px;
      height: 176px;
      background-image: repeating-linear-gradient(-45deg,#f1f2f4,#f1f2f4 14px,#000000 14px,#000000 15px);
      background-size: auto;
    }
  }

  .main-header__txt {
    margin-bottom: 0;
    margin-left: 170px;
  }

  .main-header__footer {
    margin: 0 auto;
    margin-top: -36px;
    padding: 0;

    width: 1170px;
  }

  .main-header__footer-menu {
    flex-direction: column-reverse;
    margin: 0;
    margin-top: -32px;
    padding: 0;

    width: auto;
  }
}
