.btn {
  display: block;
  margin: 0 auto;

  font-size: 16px;
  font-weight: bold;
  text-align: center;
  color: #ffffff;

  background-color: #151c22;

  border: none;

  cursor: pointer;

  &:hover {
    opacity: 0.5;
  }

  &:active {
    opacity: 0.8;
  }

  &--header {
    padding: 23px 0;

    width: 204px;
  }

  &--fashion {
    padding: 20px 0;
    width: 148px;
  }

  &--catalog {
    padding: 23px 0;
    width: 100%;
  }
}

@media(min-width: 768px) {
  .btn {
    &--header {
      margin: 0;
      margin-left: 88px;

      width: 219px;
    }

    &--fashion {
      margin: 0;
      margin-left: 50px;
    }
  }
}

@media(min-width: 1366px) {
  .btn {
    &--header {
      margin-left: 170px;
    }

    &--fashion {
      margin-left: 70px;
      padding: 23px 0;

      width: 200px;
    }
  }
}
