.main__link {
  display: block;
  margin: 0 auto;
  padding: 0;
  padding-bottom: 1px;

  width: 70px;

  border-bottom: 2px solid #151c22;

  font-family: 'WorkSans', 'Roboto', sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #151c22;
  text-align: center;
}

@media(min-width: 768px) {
  .main__link {

    display: block;

    margin: 0;
    padding: 0 5px;
    padding-top: 2px;
    padding-bottom: 2px;

    height: min-content;
        
    letter-spacing: 1px;

    &--category {
      padding-top: 3px;
    }
  }
}

@media(min-width: 1366px) {
  .main__link {
    &--category {
      padding-top: 7px;
    }
  }
}
