.footer {
  margin: 0;
  padding: 0;
  padding-bottom: 52px;

  min-width: 320px;
  width: 100%;

  background-color: #151c22;

  color: #ffffff;
}

.footer__nav {
  margin: 0 auto;
  padding: 0;
  padding-top: 46px;

  width: 290px;
}

.footer__nav-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin: 0;
  margin-bottom: 22px;
  padding: 0;

  width: 240px;

  list-style: none;
}

.footer__nav-item {
  margin: 0;
  margin-top: 5px;
  margin-right: 1px;
  margin-bottom: 17px;
  padding: 0;

  width: 80px;
  height: min-content;
}

.footer__title {
  margin: 0;
  margin-top: -5px;
  padding: 0;
}

.footer__nav-link {
  margin: 0;
  padding: 0;

  font-size: 18px;
  font-weight: 600px;
  color: #ffffff;
}

.footer__social {
  display: flex;
  justify-content: space-between;
  margin: 0;
  margin-left: 7px;
  padding:0;

  list-style: none;

  width: 177px;
}

@media(min-width: 768px) {
  .footer__nav {
    padding-top: 51px;
    width: 476px;
  }

  .footer__nav-list {
    justify-content: flex-start;
    margin: 0 auto;
    margin-bottom: 35px;
    padding: 0;
    width: 476px; 
  }

  .footer__nav-item {
    margin: 0;
    margin-right: 35px;
    padding: 0;

    width: auto;

    &:first-child {
      order: 0;
      margin: 0;
      padding: 0;

      width: 100%
    }

    &:nth-child(2) {
      order: 4;
    }

    &:nth-child(3) {
      order: 1;
    }

    &:nth-child(4) {
      order: 3;
    }

    &:nth-child(5) {
      order: 2;
    }

    &:nth-child(6) {
      order: 5;

      margin-right: 0;
    }
  }

  .footer__title {
    margin-bottom: 31px;
    text-align: center;
  }

  .footer__social {
    margin: 0 auto;
    width: 177.64px;
  }
}

@media(min-width: 1366px) {
  .footer__title {
    margin-bottom: 26px;
  }

  .footer__social {
    width: 163px;
  }
}
