@font-face {
	font-family: "WorkSans";
	src:
		local("WorkSans Regular"),
		local("WorkSans-Regular"),
		url("../font/WorkSans-Regular.woff") format("woff"),
		url("..font/WorkSans-Regular.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "WorkSans";
	src:
		local("WorkSans Medium"),
		local("WorkSans-Medium"),
		url("../font/WorkSans-Medium.woff") format("woff"),
		url("../font/WorkSans-Medium.woff2") format("woff2");
		font-weight: 600;
		font-style: normal;
}

@font-face {
	font-family: "WorkSans";
	src:
		local("WorkSans Bold"),
		local("WorkSans-Bold"),
		url("../font/WorkSans-Bold.woff") format("woff"),
		url("../font/WorkSans-Bold.woff2") format("woff2");
	font-weight: bold;
	font-style: normal;
}

@font-face {
	font-family: "WorkSans";
	src:
		local("WorkSans ExtraBold"),
		local("WorkSans-ExtraBold"),
		url("../font/WorkSans-ExtraBold.woff") format("woff"),
		url("../font/WorkSans-ExtraBold.woff2") format("woff2");
	font-weight: 800;
	font-style: normal;
}

@font-face {
	font-family: "Anton";
	src:
    local("Anton Regular"),
    local("Anton-Regular"),
		url("../font/Anton-Regular.woff") format("woff"),
		url("../font/Anton-Regular.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

body {
	margin: 0;
	padding: 0;

	min-width: 320px;
	width: 100%;

	background-color: #ffffff;

	font-family: "WorkSans", "Roboto", sans-serif;
	font-size: 16px;
	font-weight: 600;
	color: #151c22;
}

a {
	color: #151c22;
	text-decoration: none;
}

picture {
  display: block;
  margin: 0;
  padding: 0;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.sprite {
  display: none;
}

.main {
  margin: 0 auto;
  padding: 0;
  padding-bottom: 60px;
  min-width: 320px;
  max-width: 1720px;
  width: 100%;
}

.site-list {
  margin: 0;
  padding: 0;

  list-style: none;
}

.site-title {
  margin: 0;
  margin-bottom: 25px;
  padding: 0;

  font-family: "Anton", sans-serif;
  font-size: 86px;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  color: #151c22;
}

@media(min-width: 768px) {
  .main {
    padding-bottom: 42px;

    &--catalog {
      padding-bottom: 100px;
    }
  }

  .site-title {
    margin-bottom: 44px;
    margin-left: 84px;
    font-size: 98px;
    text-align: left;
  }
}

@media(min-width: 1366px) {
  .main {
    margin: 0;
    padding: 0;
    padding-bottom: 72px;

    &--catalog{
      margin-bottom:58px;
    }
  }

  .site-title {
    margin-top: -3px;
    margin-bottom: 28px;
    margin-left: 165px;
    font-size: 128px;
  }
}
