.bestsellers {
  box-sizing: border-box;
  margin: 0 auto;
  padding: 0 15px;
  padding-top: 55px;

  min-width: 320px;
  width: 320px;
}

.bestsellers__title {
  margin: 0;
  margin-bottom: 35px;
  padding: 0;

  font-size: 26px;
  font-weight: bold;
  line-height: 28px;
}

.bestsellers__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0;
  margin-bottom: 7px;
  padding: 0;

  list-style: none;
}

.bestsellers__item {
  margin: 0;
  margin-bottom: 27px;
  padding: 0;
}

.bestsellers__link {
  margin: 0;
  padding: 0;
}

.bestsellers__product-card {
  margin: 0;
  padding: 0;
}

.bestsellers__img {
  display: block;
  margin: 0;
  margin-bottom: 12px;
  padding: 0;

  & img {
    display: block;
    margin: 0;
    padding: 0;
  }
}

.bestsellers__product-description {
  margin: 0;
  padding: 0;
}

.bestsellers__product-title {
  margin: 0;
  margin-bottom: 3px;
  padding: 0;

  font-size: 16px;
  font-weight: 600;
  color: #151c22;
}

.bestsellers__product-price {
  margin: 0;
  padding: 0;

  font-size: 14px;
  font-weight: bold;
  color: #151c22;
}

@media(min-width: 768px) {
  .bestsellers {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    padding: 0 45px;
    padding-top: 96px;

    width: 768px;
  }
  .bestsellers__title {
    order: 0;
    margin: 0;
    margin-bottom: 55px;
    padding: 0;
    letter-spacing: 1px;
  }

  .bestsellers__list {
    order: 2;
  }

  .bestsellers__item {
    margin-bottom: 45px;
  }

  .bestsellers__img {
    margin-bottom: 23px;
  }

  .bestsellers__product-title {
    margin-bottom: 8px;
    font-size: 18px;
  }

  .bestsellers__product-price {
    font-size: 16px;
  }
}

@media(min-width: 1366px) {
  .bestsellers {
    padding: 0;
    padding-top: 126px;

    width: 1170px;
  }

  .bestsellers__title {
    margin-bottom: 67px;
    font-size: 32px;
    letter-spacing: 0;
  }

  .bestsellers__list {
    width: 100%;
  }
}
