.category {
  margin: 0 auto;
  padding: 0;
  padding-top: 55px;

  width: 290px;
}

.category__title {
  margin: 0;
  margin-bottom: 34px;
  padding: 0;

  font-size: 26px;
  font-weight: bold;
  color: #151c22;
}

.category__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  margin: 0;
  margin-bottom: 7px;
  padding: 0;

  list-style: none;
}

.category__item {
  margin: 0;
  margin-bottom: 26px;
  padding: 0;

  &:last-child {
    margin-top: -34px;
  }
}

.category__card {
  margin: 0;
  padding: 0;
}

.category__img {
  display: block;
  margin: 0;
  margin-bottom: 12px;
  padding: 0;
}

.category__card-title {
  margin: 0;
  padding: 0;

  font-size: 16px;
  font-weight: 600;
}

@media(min-width: 768px) {
  .category {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    padding-top: 94px;
  
    width: 678px;
  }

  .category__title {
    order: 0;

    margin-bottom: 52px;

    font-size: 28px;
  }

  .category__list {
    order: 2;
  }

  .category__item {
    margin-bottom: 46px;

    &:last-child {
      margin-top: -59px;
    }
  }

  .category__img {
    margin-bottom: 23px;
  }

  .category__card-title {
    font-size: 18px;
  }
}

@media(min-width: 1366px) {
  .category {
    padding-top: 122px;
    width: 1170px;
  }

  .category__title {
    margin-bottom: 63px;
    font-size: 32px;
  }

  .category__list {
    justify-content: space-between;
    width: 100%;
  }

  .category__item {
    &:nth-child(3) {
      order: 2;
    }

    &:last-child {
      margin: 0;
      padding: 0;
    }
  }
}
