.fashion {
  margin: 0;
  margin-top: 60px;
  padding: 0;

  min-width: 320px;
  width: 100%;
}

.fashion__collection {
  margin: 0;
  padding: 15px;
  padding-bottom: 0;

  
  background-image: repeating-linear-gradient(135deg,#fff,#fff 15px,#000 15px,#000 16px,#fff 16px);
}

.fashion__collection-wrapper {
  margin: 0 auto;
  padding: 0;
  padding-top: 37px;
  padding-bottom: 40px;

  width: 290px;

  background-color: #f1f2f4;
}

.fashion__collection-title {
  margin: 0;
  margin-bottom: 3px;
  padding: 0;

  font-size: 18px;
  font-weight: normal;
  text-align: center;
  color: #727a82;
}

.fashion__collection-txt {
  margin: 0;
  margin-bottom: 24px;
  padding: 0;

  font-size: 26px;
  font-weight: bold;
  text-align: center;
}

.fashion__img {
  display: block;
  margin: 0;
  padding: 0;

  width: 100%;
  height: auto;
}

@media(min-width: 768px) {
  .fashion {
    display: flex;

    margin: 0 auto;
    padding: 0 45px;
    padding-top: 45px;

    box-sizing: border-box;

    width: 768px;
  }

  .fashion__collection {
    order: 1;
    box-sizing: border-box;

    padding-top: 20px;
    padding-left: 0;

    width: 324px;
  }

  .fashion__collection-wrapper {
    margin: 0;
    padding-top: 46px;
    padding-bottom: 50px;

    width: 304px;
  }

  .fashion__collection-title {
    margin-left: 50px;
    text-align: left;
  }

  .fashion__collection-txt {
    margin-bottom: 33px;
    margin-left: 50px;
  
    font-size: 32px;
    text-align: left;
  }
}

@media(min-width: 1366px) {
  .fashion {
    padding: 0;
    padding-top: 73px;

    width: 1170px;
  }
  
  .fashion__collection {
    padding-top: 31px;

    width: 470px;

    background-image: repeating-linear-gradient(135deg,#fff,#fff 30px,#000 30px,#000 31px,#fff 16px);
  }

  .fashion__collection-wrapper {
    padding-top: 67px;
    padding-bottom: 70px;
    width: 400px;
  }

  .fashion__collection-title {
    margin-bottom: 8px;
    margin-left: 70px;
  }

  .fashion__collection-txt {
    margin-bottom: 43px;
    margin-left: 70px;
  }
}
