.header {
  margin: 0;
  padding: 0;

  background-color: #ffffff;

  min-width: 320px;
  width: 100%;
}

.header__wrapper {
  margin: 0 auto;
  padding: 0;
  padding-top: 21px;

  width: 320px;
}

.header__nav {
  position: relative;
  display: flex;

  margin: 0;
  padding: 0 15px;

  width: 290px;
}

.header__nav .header__hamburger {
  position: relative;
  display: block;

  margin: 0;
  margin-top: 10px;
  margin-right: 88px;
  padding: 0;

  width: 20px;
  height: 2px;

  background-color: #000000;

  border: none;

  &::before {
    content: "";
    position: absolute;

    display: block;
    margin: 0;
    padding: 0;

    width: 20px;
    height: 2px;

    top: 5px;
    left: 0;

    background-color: #000000;
  }

  &::after {
    content: "";
    position: absolute;

    display: block;
    margin: 0;
    padding: 0;

    width: 20px;
    height: 2px;

    top: 10px;
    left: 0;

    background-color: #000000;
  }
}


.header__nav-list {
  display: none;
  list-style: none;
}

.header__nav--opened {
  & .header__hamburger {
    transform: rotate(45deg);
    transition: transform 1s ease-out 0.5s;

    &::before {
      top: 0;
      left: 0;
      transform: rotate(90deg);
      transition: transform 1s ease-out 0.5s;
    }

    &::after {
      display: none;
    }
  }

  & .header__nav-list {
    position: absolute;
    display: flex;

    padding: 20px 0;

    top: 20px;
    left: 0;

    width: 100%;

    background-color: #f1f2f4;
  }
}

.header__nav-item {
  margin-left: 20px;
}

.header__nav-link {
  font-family: "WorkSans", sans-serif;
  font-size: 16px;
	font-weight: 600;
}

.header__title {
  margin: 0;
  padding: 0;
}

.header__user-nav {
  display: flex;
  margin: 0;
  margin-top: 4px;
  margin-left: auto;
  padding: 0;

  list-style: none;;
}

.header__user-nav-item {
  margin-left: 22px;
}

.header__picture {
  display: none;
}

.header__container {
  margin: 0 auto;
  padding: 0;
  padding-top: 31px;

  width: 290px;
}

.header__breadcrumbs {
  display: flex;
  margin: 0;
  padding: 0;

  list-style: none;

  font-size: 18px;
}

.header__breadcrumbs-item {
  &--tablet-show {
    display: none;
  }
}

.header__breadcrumbs-link {
  color: #727a82;
}

.header__breadcrumbs-separator {
  margin: 0;
  margin-right: 11px;
  margin-left: 5px;
  padding: 0;

  color: #727a82;
}

.header__breadcrumbs-current-item {
  display: inline-block;
  margin: 0;
  padding: 0;

  color: #151c22;
}

.header__main-title {
  color: inherit;
  font-size: 0;
}

@media(min-width: 768px) {
  .header {
    margin: 0;
    padding: 0;
    width: 100%;

    background-color: #f1f2f4;
  }

  .header__wrapper {
    margin: 0;
    padding: 0;
    width: 100%;
  }

  .header__nav-wrapper {
    margin: 0;
    padding: 0;
    background: #ffffff;
    width: 100%;
  }

  .header__nav {
    margin: 0 auto;
    padding: 0;
    padding-top: 21px;
    padding-bottom: 19px;
    width: 678px;
  }

  .header__nav .header__hamburger,
  .header__nav--opened .header__hamburger,
  .header__hamburger {
    display: none;
  }

  .header__nav-list {
    display: flex;
    justify-content: space-between;
    margin: 0;
    margin-right: 105px;
    padding: 0;
    padding-top: 7px;

    width: 197px;
  }

  .header__nav-item {
    margin: 0;
    padding: 0;
  }

  .header__user-nav {
    margin-top: 2px;
  }

  .header__user-nav-item {
    margin-left: 35px;
  }

  .header__container {
    display: flex;
    justify-content: space-between;

    margin: 0 auto;
    padding: 0;

    width: 678px;
  }

  .header__breadcrumbs {
    margin-top: 76px;
    margin-bottom: 27px;
  }

  .header__breadcrumbs-item {
    &--tablet-show {
      display: block;
    }
  }

  .header__main-title {
    margin: 0;
    padding: 0;

    font-size: 28px;
    color: #151c22;
  }

  .header__picture {
    position: relative;
    display: block;
    margin: 0;
    margin-right: 30px;
    padding: 0;

    & img {
      display: block;
    }

    &::before {
      content: "";
      position: absolute;
      display: block;

      bottom: 0;
      left: -30px;

      width: 30px;
      height: 135px;

      background-image: repeating-linear-gradient(45deg,#f1f2f4,#f1f2f4 7px,#000000 7px,#000000 8px);
      background-size: auto;
  
    }

    &::after {
      content: "";
      position: absolute;
      display: block;

      top: 0;
      right: -30px;
      width: 30px;
      height: 176px;
      background-image: repeating-linear-gradient(45deg,#f1f2f4,#f1f2f4 7px,#000000 7px,#000000 8px);
      background-size: auto;
    }
  }
}

@media(min-width: 1366px) {
  .header__nav {
    width: 1170px;
  }

  .header__nav-list {
    margin-right: 351px;
  }

  .header__container {
    width: 1170px;
  }

  .header__title-wrapper {
    margin-left: 200px;
  }

  .header__breadcrumbs {
    margin-top: 70px;
  }

  .header__main-title {
    font-size: 32px;
    line-height: 1.25;
  }

  .header__picture {
    margin-right: 270px;

    &::after {
      background-image: repeating-linear-gradient(45deg,#f1f2f4,#f1f2f4 10px,#000 10px,#000 11px);
    }
  }
}
