.catalog {
  margin: 0 auto;
  padding: 0;
  padding-top: 26px;

  width: 290px;
}

.catalog__header {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;

  width: 100%;
}

.catalog__filters-btn {
  position: relative;
  margin: 0;
  padding: 0 15px;
  padding-top: 12px;
  padding-bottom: 11px;

  border: 2px solid #151c22;
  background-color: #ffffff;

  width: 100%;

  font-size: 18px;
  font-weight: 600;
  color: #151c22;
  text-align: left;

  &::after {
    position: absolute;
    content: "";
    display: block;

    width: 10px;
    height: 10px;

    transform: rotate(45deg);
    right: 17px;
    top: 12px;

    border-right: 2px solid #151c22;
    border-bottom: 2px solid #151c22;
  }
}

.catalog__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  width: 290px;

  list-style: none;
}

.catalog__item {
  margin: 0;
  margin-bottom: 27px;
  padding: 0;
}

.catalog__card {
  margin: 0;
  padding: 0;

  & picture {
    display: block;

    margin: 0;
    padding: 0;
  }
}

.catalog__card-img {
  display: block;
  margin: 0;
  padding: 0;
}

.catalog__card-description {
  margin: 0;
  padding: 0;
  padding-top: 12px;
}

.catalog__card-title {
  margin: 0;
  margin-bottom: 3px;
  padding: 0;

  font-size: 16px;
  font-weight: 600;
  color: #151c22;
}

.catalog__card-price {
  margin: 0;
  padding: 0;

  font-size: 14px;
  font-weight: bold;
  color: #151c22;
}

.catalog__info {
  margin: 0 auto;
  padding: 0;

  width: 177px;
}

.catalog__info-count {
  margin: 0;
  margin-bottom: 9px;
  padding: 0;
}

.catalog__info-indicate {
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  padding: 0;

  width: 100%;
  height: 2px;

  background-color: #e5e5e5;
}

.catalog__info-current-indicate {
  position: absolute;
  top: 0;
  left: 0;

  width: 20%;
  height: 2px;

  background-color: #151c22;
}

@media(min-width: 768px) {
  .catalog {
    padding-top: 24px;
    width: 678px;
  }

  .catalog__header {
    margin-bottom: 25px;
  }

  .catalog__filters-btn {
    display: block;
    margin-right: 0;
    margin-left: auto;
    padding: 14px;

    width: 207px;

    &::after {
      top:15px;
      right: 15px;
    }
  }

  .catalog__list {
    margin-bottom: 8px;
    width: 678px;
  }

  .catalog__item {
    margin-bottom: 46px;

    &--tablet-margin {
      margin-top: -391px;
      width: 100%;
    }
  }

  .catalog__card-description {
    padding-top: 21px;
  }

  .catalog__card-title {
    margin-bottom: 8px;
    font-size: 18px;
  }

  .catalog__card-price {
    font-size: 16px;
  }
}

.catalog__info {
  width: 206px;
}

.catalog__info-count {
  margin-bottom: 4px;

  font-size: 18px;
  font-weight: normal;
  text-align: center;
}

.catalog__info-indicate {
  margin-bottom: 40px;
}

@media(min-width: 1366px) {
  .catalog {
    padding-top: 39px;
    width: 1170px;
  }

  .catalog__header {
    margin-bottom: 39px;
  }

  .catalog__filters-btn {
    padding-left: 15px;
    width: 243px;

    &::after {
      right:18px;
    }
  }

  .catalog__list {
    margin-bottom: 3px;
    width: 1170px;
  }

  .catalog__item {
    margin-bottom: 64px;
  
    &--big {
      order: 10;
      margin-top: -803px;
      margin-right: 0;
      margin-left: auto;
    }
    
    &--tablet-margin {
      margin-top: 0;
      width: auto;
    }

    &--desktop-margin {
      margin-top: 0;
      width: 73.5%;
    }
  }

  .catalog__info {
    width: 200px;
  }
}
