body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  width: 100%;
  background-color: #ffffff;
  font-family: "WorkSans", "Roboto", sans-serif;
  font-size: 16px;
  font-weight: 600;
  color: #151c22;
}
a {
  color: #151c22;
  text-decoration: none;
}
picture {
  display: block;
  margin: 0;
  padding: 0;
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.sprite {
  display: none;
}
