}
.header__nav .header__hamburger::before {
  content: "";
  position: absolute;
  display: block;
  margin: 0;
  padding: 0;
  width: 20px;
  height: 2px;
  top: 5px;
  left: 0;
  background-color: #000000;
}
.header__nav .header__hamburger::after {
  content: "";
  position: absolute;
  display: block;
  margin: 0;
  padding: 0;
  width: 20px;
  height: 2px;
  top: 10px;
  left: 0;
  background-color: #000000;
}
